/** @jsx jsx */
import { jsx, css } from '@emotion/react'

import ProductVariantButtons from '../variant-buttons'
import ProductVariantDropdowns from '../variant-dropdowns'
import ClearSelections from '../clear-selections'

function ProductOptions({
	variantStyle,
	availableOptions,
	missingSelections,
	selectedOptions,
	availableVariants,
	variants,
	settings,
	allSelectableOptions,
	hasSelections,
	isDirectCheckoutOut,
}) {
	const ProductOptionsCSS = css`
		position: relative;
	`
	return (
		<div
			className='wps-product-options'
			css={ProductOptionsCSS}
			aria-label={`Product variant ${
				variantStyle ? variantStyle : 'dropdown'
			}`}>
			{hasSelections && !isDirectCheckoutOut ? (
				<ClearSelections settings={settings} />
			) : null}
			{variantStyle === 'dropdown' ? (
				<ProductVariantDropdowns
					options={availableOptions}
					missingSelections={missingSelections}
					availableVariants={availableVariants}
					selectedOptions={selectedOptions}
					variants={variants}
					settings={settings}
					allSelectableOptions={allSelectableOptions}
				/>
			) : variantStyle === 'buttons' && shopwp.misc.isPro ? (
				<ProductVariantButtons
					options={availableOptions}
					missingSelections={missingSelections}
					availableVariants={availableVariants}
					selectedOptions={selectedOptions}
					variants={variants}
					settings={settings}
					allSelectableOptions={allSelectableOptions}
				/>
			) : (
				<ProductVariantDropdowns
					options={availableOptions}
					missingSelections={missingSelections}
					availableVariants={availableVariants}
					selectedOptions={selectedOptions}
					variants={variants}
					settings={settings}
					allSelectableOptions={allSelectableOptions}
				/>
			)}
		</div>
	)
}

export default ProductOptions
