/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useProductDispatch } from "../../_state/hooks"

import Quantity from "../../../../quantity"

function ProductQuantity({ minQuantity, maxQuantity, quantityStep }) {
  const productDispatch = useProductDispatch()

  function onChange(newQuantity) {
    productDispatch({ type: "UPDATE_QUANTITY", payload: newQuantity })
  }

  return (
    <Quantity
      dispatch={productDispatch}
      quantityStep={quantityStep}
      maxQuantity={maxQuantity}
      minQuantity={minQuantity}
      initialQuantity={minQuantity > 1 ? minQuantity : 1}
      onChange={onChange}
      small={false}
    />
  )
}

export default ProductQuantity
